.modal-signin__backdrop {
  position: relative;
  position: fixed;
  z-index: 100;

  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  transition: opacity linear 200ms;

  overflow-y: scroll;
}
.modal-signin__container {
  position: absolute;
  top: 25%;
  left: 50%;
  transform: translateX(-50%);
  margin: 0;
  padding: 50px 0px;
  position: relative;
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: column;
  width: 275px;
  background-color: $white-color;
  box-shadow: 10px 5px 5px rgba(0, 0, 0, 0.144);
  @media screen and (min-width: $tablet) {
    width: 450px;
  }
  @media screen and (min-width: $desktop) {
    width: 600px;
  }
}
.modal-signin__close-btn {
  display: flex;
  position: absolute;
  padding: 0;
  border: none;
  top: 16px;
  right: 16px;
  cursor: pointer;
  width: 14px;
  height: 14px;
  background-color: transparent;
}
.modal-signin__title {
  margin-bottom: 30px;
  font-family: $font-family;
  font-weight: $medium-weight;
  font-size: 2rem;
  line-height: 2.3rem;
  color: $main-color;
  text-transform: uppercase;

  @media screen and (min-width: $desktop) {
    font-size: 30px;
    line-height: calc(35 / 30);
  }
}
