.modal-signin__button {
  display: flex;
  gap: 10px;
  display: flex;
  align-items: center;
  padding: 10px;
  justify-content: center;
  background-color: $accent-color;
  color: $white-color;
  border: none;
  outline: transparent;
  width: 150px;
  border-radius: 5px;
  font-family: $font-family;
  font-weight: $medium-weight;
  font-size: 1.5rem;
}
